/*@import "~normalize.css";*/
@import "~highlight.js/styles/github.css";

@font-face {
  font-family: "pub";
  src: local('pub'), url(../src/fonts/PublicSans-Regular.woff) format('woff');
}

@font-face {
  font-family: "all";
  src: local('all'), url(../src/fonts/alliance.woff) format('woff');
}

body {
  margin: 0;
  padding: 0;
  background-color: rgb(9, 8, 33);
  font-family: "all", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
  color: inherit;
}